import {
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Label
  // CustomInput
} from 'reactstrap'
import BreadCrumbs from '@components/breadcrumbs'
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import React, { Fragment, useState } from 'react'
import {  addfootballcontestprize} from '../../store/actions'
import { useDispatch } from 'react-redux'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import { useForm, Controller } from 'react-hook-form'

const selectcategory = [
  { value: 'Fashion', label: 'Fashion' },
  { value: 'Coupon', label: 'Coupon' },
  { value: 'Electronics', label: 'Electronics' }
]
const VerticalForm = () => {
  const defaultValues = {
    reactSelect: null
  }

  const { register, errors, handleSubmit, control } = useForm({defaultValues})
  const [err, setErr] = useState(false)
  const dispatch = useDispatch()
  const [prizeform, setPrizeform] = useState({ contest_id:"", name:"", prize_rank:"", caption:"", value:"", type:"", assets_url:""})
  
  const onSubmit = (e) => {
    e.preventDefault()
    setErr(true)
    dispatch(addfootballcontestprize(prizeform))
  }

  const onclear = (e) => {
    e.preventDefault()
    setPrizeform({ contest_id:"", name: '', caption:'', value: '',  assets_url: '' })
  }

const editcategory = (e) => {
    setErr(false)
    console.log(err)
    setPrizeform({...prizeform, type:e.value})
  }

const convertBase64 = (file) => {
  return new Promise((resolve, reject) => {
    const fileReader = new FileReader()
    fileReader.readAsDataURL(file)    
    fileReader.onload = () => {
      resolve(fileReader.result)
    }    
    fileReader.onerror = (error) => {
      reject(error)
    }
  })
}

const uploadImage = async (e) => {
  const file = e.target.files[0]
  const base64 = await convertBase64(file)
  setPrizeform({ ...prizeform, assets_url: base64 })
}

  return (
    <Fragment>
     <BreadCrumbs breadCrumbTitle='Football Contestsprize' breadCrumbParent='contestsprize' breadCrumbActive='Add' />
    <Card>
      <CardHeader>
        <CardTitle tag="h4">Add Cricket Contest Prizes</CardTitle>
      </CardHeader>
      <CardBody>
        <Form onSubmit={handleSubmit(onSubmit)}>
          <Row>
          <Col sm="12">
              <FormGroup>
                <Label for="id">ID</Label>
                <Input
                  type="number"
                  name="id"
                  id="id"
                  innerRef={register({ required: true })}
                  invalid={errors.id && true}
                  value={prizeform.contest_id}
                  onChange={(e) => setPrizeform({ ...prizeform, contest_id: e.target.value })}
                  placeholder="Enter No of ID "
                />
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for='name'>Name</Label>
                <Input
                  type="text"
                  name="name"
                  id="name"
                  innerRef={register({ required: true })}
                  invalid={errors.name && true}
                  value={prizeform.name}
                  onChange={(e) => setPrizeform({ ...prizeform, name: e.target.value })}
                  placeholder="Enter Product Name"
                />
              </FormGroup>
            </Col>
            <Col>
              <FormGroup>
                <Label for="caption">Caption</Label>
                <Input
                  type="text"
                  name="caption"
                  id="caption"
                  innerRef={register({ required: true })}
                  invalid={errors.caption && true}
                  value={prizeform.caption}
                  onChange={(e) => setPrizeform({ ...prizeform, caption: e.target.value })}
                  placeholder="Enter Product Name"
                />
              </FormGroup>
            </Col>
            <Col>
              <FormGroup>
                <Label for="nameVertical">Category</Label>
                <Controller
              control={control}
              render={({ field: { ...field } }) => (
                <Select
                  {...field}
                  defaultValue={selectcategory[0]}                 
                  onChange={(e) => editcategory(e)}
                  options={selectcategory}
                  theme={selectThemeColors}
                  classNamePrefix='select'
                  />
                  )}
                  name='reactSelect'
            />
                {/* <Select
                  theme={selectThemeColors}
                  className="react-select"
                  classNamePrefix="select"
                  onChange={(e) => editcategory(e)}
                  options={selectcategory}
                  isClearable={false}
                /> */}
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for="caption">coins</Label>
                <Input
                  type="number"
                  name="coins"
                  id="caption"
                  innerRef={register({ required: true })}
                  invalid={errors.coins && true}
                  value={prizeform.value}
                  onChange={(e) => setPrizeform({ ...prizeform, value: e.target.value })}
                  placeholder="Enter No of Coins "
                />
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for="prize">Prize Rank</Label>
                <Input
                  type="number"
                  name="prize"
                  id="prize"
                  innerRef={register({ required: true })}
                  invalid={errors.prize && true}
                  value={prizeform.prize_rank}
                  onChange={(e) => setPrizeform({ ...prizeform, prize_rank: e.target.value })}
                  placeholder="Enter No of ID "
                />
              </FormGroup>
            </Col>          
            <Col sm="12">
              <FormGroup>
                <Label for="customFile">Custom File Input</Label>
                <Input
                  type="file"
                  id="customFile"
                  name="customFile"
                  innerRef={register({ required: true })}
                  invalid={errors.customFile && true}
                  onChange={(e) => uploadImage(e)}
                />
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup className="d-flex mb-0">
                <Button.Ripple
                  className="mr-1"
                  color="primary"
                  type="submit"
                >
                  Submit
                </Button.Ripple>
                <Button.Ripple
                  outline
                  color="secondary"
                  type="reset"
                  onClick={(e) => onclear(e)}
                >
                  Reset
                </Button.Ripple>
              </FormGroup>
            </Col>
          </Row>
        </Form>
      </CardBody>
    </Card>
    </Fragment>
  )
}
export default VerticalForm
