import http from '../../../../configs/httpConfig'

export const clearData = () => {
  return {type:'CLEAR_DATA'}
}

export const getAllCricketcontestprize = () => ({
  type: 'GET_ALL_CRICKET_CONTEST_PRIZE'
})

export const getAllCricketcontestFail = () => ({
  type: 'GET_ALL_USERS_FAIL'
})
export const cleardata = () => ({
  type: 'CLEAR_DATA'
})
export const getCricketContests = (page, dataperPage = '') => {
  return async (dispatch) => {
    dispatch(getAllCricketcontestprize())
    await http.get(`cricket/contest_prizes/list?page=${page}&limit=${dataperPage}`).then((response) => {
      dispatch({
        type: 'CRICKET_CONTESTS_PRIZR',
        data: response.data.data
      })
    }).catch(() => {
      dispatch(getAllCricketcontestFail())
    })
  }
}


export const getCricketContestadd = (params) => {
  return (dispatch) => {
    // dispatch(getAllCricketcontestprize())
    http
      .post('/cricket/contest_prizes/list', params)
      .then((response) => {
        dispatch({
          type: 'CRICKET_CONTEST_PRIZE_ADD',
          data: response.data.data
        })
        console.log(response.data.data)
      })
      .catch((err) => {
        console.log(err)
      })
  }
}


export const addcricketcontestprize = (add) => {

  return (dispatch) => {
    
    http
    .post('/cricket/contest_prizes/add', add)
    .then((response) => {
      dispatch({
        type: 'ADD_CRICKET_CONTEST_PRIZE',
        data: response.data
      })  
      console.log(response)  
    })
    .catch((err) => console.log(err))
  }
}

