export const deletecricketcontestprizes = (id) => {
  return (dispatch) => {
     return http.delete(`cricket/contest_prizes/delete/${id}`).then((response) => {
       console.log(id)
       dispatch({ type: 'DELETE_PRIZE', data: response.data })
       dispatch(getCricketContests())
     })
   }
 }

